**子组件代码   与c页面子父通信**
<template>
  <header>
    <p @click="box">这是子组件代码</p>
    {{thisTitleTxt}}
  </header>
</template>
<script>
  export default {
    props: {
      titleTxt: String
    },
    data () {
      return {
        thisTitleTxt: this.titleTxt
      }
    },
    methods:{
      box(){
        alert(1)
      }
    }
  }
</script>



